import React from 'react'
import './project-card.scss'

function ProjectCard(project) {
    const {title, image, description} = project;

    return (
        <div className="project-card">
            <h4>{title}</h4>
            <img id='image' src={ image }/>
            <p>{description}</p>
        </div>
    )
}

export { ProjectCard };