import React, { useState } from 'react';
import { Button } from 'react-bootstrap';
import { Link } from 'react-scroll';
import './Navbar.scss';

import website_logo from '/Users/emilydoan/emily-doan/src/assets/website-logo.png';

function Navbar() {
    const [showDropMenu, setShowDropMenu] = useState(false);

    const toggleDropDownMenu = () => {
        setShowDropMenu(!showDropMenu);
    }

    return (
        <div className={'navbar' + (showDropMenu ? 'show-menu' : '')}>
            <div className="website-logo">
                <Link to="hero" smooth={true} duration={500}>
                    <img id='website-logo' alt="website-logo" src={ website_logo }/>
                </Link>
            </div>
            <Button onClick={toggleDropDownMenu} className='menu-button btn-secondary'> 
                {showDropMenu ? 'x' : ''}
            </Button>
            <div className={'nav-right' + (showDropMenu ? 'show' : '')}>
                <Link to="about" smooth={true} duration={500}>
                    <p>
                        about
                    </p>
                </Link>
                <Link to="skills" smooth={true} duration={500}>
                    <p>
                        skills
                    </p>
                </Link>
                <Link to="projects" smooth={true} duration={500}>
                    <p>
                        projects
                    </p>
                </Link>
            </div>
        </div>
    )
}

export default Navbar;